@import "styles/variables.scss";

.subTitle {
    font-weight: 400;
}

.info,
.info table {
    width: 100%;
}

.info table {
    border-collapse: collapse;
}

.info td,
.info th {
    line-height: 100%;
    text-align: center;
    width: 33%;
    height: 100px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    padding: 22px 0;
    margin: 0;
    box-sizing: border-box;
}

.info td > p,
.info th > p {
    padding: 2px 24px;
}

.info td p.tableTitle,
.info th p.tableTitle {
    text-transform: uppercase;
}

.description {
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    margin-top: 40px;
    max-width: 673px;
}

.infoSubTitle {
    text-transform: uppercase;
    margin-bottom: 24px;
}

.infoDescription {
    margin-bottom: 32px;
    max-width: 673px;
}

@media #{$tablet} {
    .info td,
    .info th {
        padding: 10px 0;
    }
    .info td > p,
    .info th > p {
        padding: 1px 5px;
    }
    .description {
        margin-top: 32px;
    }
    .infoSubTitle {
        margin-bottom: 16px;
        font-size: 16px;
    }
    .infoDescription {
        margin-bottom: 24px;
    }
}