@import "styles/variables.scss";

.colContainer {
    display: flex;
}

@media screen and (max-width: 1680px) {
    .content {
        margin-left: 140px;
        margin-right: 40px;
    }
}

@media #{$tablet} {
    .content {
        margin-left: 20px;
        margin-right: 20px;
    }
    .colContainer {
        flex-direction: column;
    }
    .colContainer > div:first-child {
        margin-bottom: 44px;
    }
}