@import "styles/variables.scss";

.returnCol > .infoDescription {
    margin-bottom: 24px;
}

.infoDescription {
    margin-bottom: 32px;
    max-width: 673px;
}

.infoDescription > a {
    text-decoration: none;
    border-bottom: 1px solid $darkBlue;
}

.infoSubTitle {
    text-transform: uppercase;
    margin-bottom: 24px;
}

.miniMargin {
    margin-bottom: 4px;
}

.miniMargin + .infoDescription {
    max-width: 265px;
}

@media #{$tablet} {
    .returnCol > .infoDescription {
        margin-bottom: 16px;
    }
    .priceCol > .infoDescription {
        margin-bottom: 24px;
    }
    .infoSubTitle {
        font-size: 16px;
        margin-bottom: 16px;
    }
    .miniMargin {
        margin-bottom: 4px;
    }
}