@import "styles/variables.scss";

.navbar {
    margin-right: 10vw;
    width: fit-content;
}

.navbar > p {
    text-transform: uppercase;
    margin-bottom: 16px;
    opacity: 0.4;
    cursor: pointer;
    white-space: nowrap;
}

@media #{$tablet} {
    .navbar > p {
        font-size: 16px;
    }
}